.about {
  background-color: #f2f2f1;
  min-height: 757px;
}
.about-img__wrap {
  position: relative;
}
.about-img img {
  position: absolute;
  top: 0;
  left: -210px;
  max-width: none;
}
.about-descr {
  padding-top: 96px;
}
.about-descr__title {
  margin-bottom: 40px;
}
.about-descr__text {
  margin-bottom: 46px;
  &: last-child {
    margin-bottom: 70px;
  }
}
.about-item {
  padding-left: 65px;
}
.about-item--web,
.about-item--graphic,
.about-item--seo,
.about-item--online {
  position: relative;
}
.before {
  content: '';
  width: 41px;
  height: 41px;
  position: absolute;
  top: 0;
  left: 0;
  background-image: url('../img/about-items.png');
  background-repeat: no-repeat;
}
.about-item--web::before {
  content: '';
  width: 41px;
  height: 41px;
  position: absolute;
  top: 0;
  left: 0;
  background-image: url('../img/about-items.png');
  background-repeat: no-repeat;
  background-position: -5px -152px;
}
.about-item--graphic::before {
  content: '';
  width: 31px;
  height: 45px;
  position: absolute;
  top: 0;
  left: 0;
  background-image: url('../img/about-items.png');
  background-repeat: no-repeat;
  background-position: -5px -51px;
}
.about-item--seo::before {
  content: '';
  width: 37px;
  height: 36px;
  position: absolute;
  top: 0;
  left: 0;
  background-image: url('../img/about-items.png');
  background-repeat: no-repeat;
  background-position: -5px -5px;
}
.about-item--online::before {
  content: '';
  width: 36px;
  height: 36px;
  position: absolute;
  top: 0;
  left: 0;
  background-image: url('../img/about-items.png');
  background-repeat: no-repeat;
  background-position: -5px -106px;
}
.about-item--web,
.about-item--graphic {
  margin-bottom: 50px;
}
.about-item__title {
  margin-bottom: 30px;
}
@media only screen and (max-width: 1200px) {
  .about-img img {
    position: relative;
  }
}
@media only screen and (max-width: 992px) {
  .about-img__wrap {
    text-align: center;
    width: 100%;
  }
  .about-img img {
    position: relative;
    top: 0;
    left: 0;
  }
}
@media only screen and (max-width: 480px) {
  .about-img {
    display: none;
  }
}
.about-item--online {
  margin-bottom: 40px;
}
.about-item__title {
  margin-bottom: 15px;
}
