/*Нaстройка скрола страницы*/

.mPS2id-highlight,
.mPS2id-highlight:hover,
.mPS2id-highlight:focus,
.mPS2id-highlight:active {
  color: #26cb72!important;
  background-color: #252e30 !important;
}
/*кнопка скрола возврата в начало лендинга*/

.scroll-top-wrapper {
  position: fixed;
  opacity: 0;
  visibility: hidden;
  overflow: hidden;
  text-align: center;
  z-index: 99999999;
  background-color: #777777;
  color: #eeeeee;
  width: 50px;
  height: 48px;
  line-height: 48px;
  right: 30px;
  bottom: 30px;
  padding-top: 2px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
  transition: all 0.5s ease-in-out;
}
.scroll-top-wrapper:hover {
  background-color: $green;
}
.scroll-top-wrapper.show {
  visibility: visible;
  cursor: pointer;
  opacity: 1.0;
}
.scroll-top-wrapper i.fa {
  line-height: inherit;
}
