.navbar {
  position: relative;
  max-width: 1600px;
  margin: 0 auto;
  min-height: 70px!important;
  border-radius: 0;
}
.navbar.default {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 999;
  transition: all 0.5s;
}
.navbar-brand {
  float: left;
  padding: 15px 10px;
  font-size: 18px;
  line-height: 20px;
  min-height: 70px!important;
}
.navbar-default {
  background-color: #252e30;
  border-color: #252e30;
}
.navbar-default .navbar-collapse,
.navbar-default .navbar-form {
  border-color: #252e30;
}
.navbar-default .navbar-nav > .active > a,
.navbar-default .navbar-nav > .active > a:hover,
.navbar-default .navbar-nav > .active > a:focus {
  color: #2ecc71!important;
  background-color: #252e30;
}
.navbar-default .navbar-nav > .active > a,
.navbar-default .navbar-nav > .active > a:hover,
.navbar-default .navbar-nav > .active > a:focus {
  color: #2ecc71!important;
  background-color: #252e30;
}
.navbar-nav > li > a {
  padding-top: 0;
  padding-bottom: 0;
  line-height: 70px;
  vertical-align: middle;
  color: #ffffff!important;
  text-transform: uppercase;
}
.nav > li > a {
  position: relative;
  display: block;
  padding: 0 10px;
  background-color: transparent;
  transition: all 0.5s;
}
.nav-link:hover {
  color: #2ecc71!important;
}
.navbar-default .navbar-toggle {
  background-color: transparent;
  border-color: $main;
}
.fa--white {
  color: $main;
  font-size: 30px;
  background-color: transparent;
}
.navbar-toggle {
  position: relative;
  float: right;
  margin-right: 10px;
  padding: 6px 10px;
  margin-top: 12px;
  margin-bottom: 8px;
  background-color: transparent;
  background-image: none;
  border: 1px solid transparent;
}
#nav.affix {
  top: 0;
  width: 100%;
}
