
section.hobbi {
  padding-top: 90px;
  margin-bottom: 90px;
}

.hobbi-item__title {
  margin-bottom: 40px;
}

@media only screen and (max-width: 768px) {


  .hobbi-items {
    padding: 90px 10px;
  }

  .hobbi-item__title {
    text-align: center;
  }

  .hobbi-item__text {
    text-align: justify;
  }

}

@media only screen and (min-width: 320px) {
  .hobbi-items {
    padding: 20px 20px;
  }

  .hobbi-item {
    padding-top: 40px;
  }

  .hobbi-item__title {
    font-size: 26px;
    margin-bottom: 20px;
  }

  .hobbi-item__text {
    font-size: 15px;
  }
}
