.contact {
  padding: 90px 0;
  background-color: $gray;
}
.contact-descr {
  padding-right: 40px;
  margin-bottom: 60px;
}
.contact__title {
  color: #fff;
}
.contact__text {
  margin: 30px 0 40px 0;
}
.contact-form {
  width: 100%;
  margin: 0 auto;
}
.contact__address-span {
  color: #fff;
  margin-bottom: 20px;
}
.contact__address-span-link {
  margin-left: 28px;
  &: hover {
    color: #fff;
  }
}
.contact__address-span--phone {
  margin-left: 20px;
}
