.carousel-caption--work {
  top: 20%;
}
.slider__text--work {
  top: 30%;
  font-size: 1.5rem;
  font-style: italic;
  font-family: $r;
  margin-bottom: 40px;
}
.carousel-indicators--work {
  bottom: 30%;
}
.carousel-indicators--work li {
  color: $green;
  border: 1px solid $green;
}
.carousel-indicators--work .active {
  background-color: $green;
}
@media only screen and (max-width: 1368px) {
  .carousel-indicators--work {
    bottom: 20%;
  }
}
@media only screen and (max-width: 1024px) {
  .carousel-indicators--work {
    bottom: 5%;
  }
}
@media only screen and (max-width: 768px) {
  .carousel-caption--work {
    top: -6%;
  }
  .carousel-indicators--work {
    bottom: 10%;
  }
}
