.resume {
  background-color: #f2f2f1;
  min-height: 420px;
}
.resume-block {
  padding: 96px 0;
  position: relative;
  &:after {
    content: '';
    position: absolute;
    left: 50%;
    margin-left: -50px;
    bottom: -88px;
    border: 50px solid transparent;
    border-top: 40px solid #f2f2f1;
    z-index: 10;
  }
}
.resume-block__title {
  margin-bottom: 50px;
}
.resume-block__text {
  margin-bottom: 46px;
  padding: 0 201px;
}
@media only screen and (max-width: 992px) {
  .resume-block__text {
    padding: 0;
  }
}

