.portfolio {
  padding: 90px 0;
}
.portfolio-decsr {
  padding: 0 90px;
}
.portfolio-decsr__title {
  margin-bottom: 36px;
}
.portfolio-decsr__text {
  margin-bottom: 86px;
}
.portfolio-item {
  max-width: 366px;
  max-height: 299px;
  margin-bottom: 20px;
}
img .portfolio-item__img {
  max-width: 100%;
}
.portfolio-item {
  position: relative;
}
.portfolio-item__hover {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: $green;
  opacity: 0;
  transition: all 0.7s;
  transform: scale(0);
  font-size: 40px;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}
.portfolio-item__link:hover .portfolio-item__hover {
  opacity: 0.5;
  transform: scale(1);
}

@media only screen and (max-width : 992px) {
  .portfolio {
  padding: 0 0 90px 0;
}
}

@media only screen and (max-width: 768px) {
  .portfolio-items {
    margin-left: 36px;
  }
}
@media only screen and (max-width: 480px) {
  .portfolio-items {
    margin-left: 20px;
  }
  .portfolio-decsr__text {
    display: none;
  }
}
@media only screen and (max-width: 479px) {
  .portfolio-decsr__title {
    font-size: 22px;
  }
}
