.footer {
  padding: 44px 0;
  background-color: #191f21;
}
.footer-descr__title {
  margin-bottom: 24px;
}
.footer-social__items {
  display: flex;
  justify-content: center;
}
.footer-social__item {
  margin-right: 6px;
  color: $text;
}
.footer-social__item-link {
  transition: all 0.5s;
  &:hover {
    color: $line;
    background-color: transparent;
  }
}
@media only screen and (max-width: 480px) {
  .footer-social__item {
    margin: 0;
    font-size: 8px;
  }
  .footer-descr__title {
    margin-bottom: 24px;
    padding: 0 20px;
  }
}
