.slider {
  overflow-x: hidden;
}
.carousel-caption--slider-top {
  top: 50%;
}
.slider__title--slider-top {
  width: 80%;
  margin: 0 auto;
  border-top: 2px solid;
  border-bottom: 2px solid;
  margin-bottom: 46px;
}
.carousel-indicators--slider-top {
  width: 10%;
  transform: rotate(90deg);
  left: 80%;
  bottom: 140px;
  margin-left: 0;
}
@media only screen and (max-width: 1590px) {
  .slider__title--slider-top {
    width: 100%;
  }
}
@media only screen and (max-width: 1270px) {
  .slider__title--slider-top {
    font-size: 44px;
  }
}
@media only screen and (max-width: 768px) {
  .slider__title--slider-top {
    font-size: 34px;
  }
  .carousel-caption--slider-top {
    top: 40%;
  }
}
